.header {
  box-sizing: border-box;
  height: 100vh;
  width: 100%;

  display: flex;
  flex-direction: column;


  &__bottom {
    padding: 0 30px;
  }


  &__title {
    color: $main-color;
    font-family: 'Inter';
    font-size: 40px;
    font-weight: 700;
    line-height: 52px;
    text-transform: uppercase;
    margin: 0;
    margin-bottom: 20px;
  }

  &__text {
    color: $main-color;
    font-family: 'Inter';
    font-size: 16px;
    font-weight: 00;
    line-height: 24px;
    margin: 0;
  }

  &__bg {
    margin-bottom: 40px;
    height: 245px;
    width: 100%;
    background-image: url(../images/mobile-bg.png);
    background-position: center;
    background-size: cover;

  }
}
