.page {
  font-family: 'Inter', sans-serif;
  font-family: 'Space Grotesk', sans-serif;
  margin: 0;
  padding: 0;
  background-color: $background-color;

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transition: all 0.3s;
    opacity: 0;
    transform: translateX(-100%);
    pointer-events: none;
    overflow: auto;

    &:target {
      opacity: 1;
      transform: translateX(0);
      pointer-events: all;
  }
  }
}
